.form-main {
  text-align: center;
  font-size: 16px;
  padding: 5px;
}

.form-input {
  border: 1px solid lightgray;
  border-radius: 20px;
  background-color: azure;
  margin: 5px;
  font-size: 20px;
  padding: 5px;
  color: green;
}
.form-button {
  border: 1px solid black;
  border-radius: 20px;
  width: 150px;
  padding: 5px;
  margin: 5px;
  background-color: blue;
  color: azure;
}

::-webkit-input-placeholder {
  color: green;
}

::-ms-input-placeholder {
  color: green;
}

::placeholder {
  color: green;
}



